import Janus from './janus/janus.js';

let server = null;
let janus = null;

let screenHandle = null;
let videoHandlerOnPC = null;

let myId = null;
let room = null;

let opaqueId = ''; //  ?
const TEST_CANDIDATE_NUM = 5;

if (window.location.protocol === 'http:')
	server = 'http://' + 're-coder.net' + '/janus';
else server = 'https://' + 're-coder.net' + '/janus';

export function runJanusMobile(studentId, testId) {
	Janus.init({
		debug: 'all',
		callback: function () {
			janus = new Janus({
				server: server,
				opaqueId: opaqueId,
				success: function () {
					// VideoRoom plugin 
					janus.attach({
						plugin: 'janus.plugin.videoroom',
						opaqueId: opaqueId,

						success: function (pluginHandle) {
							videoHandlerOnPC = pluginHandle;
							
							myId = Number(studentId) + 2;
							room = Number(testId);
							
							Janus.log(
								`Plugin attached! (${videoHandlerOnPC.getPlugin()}
                                    , Display Value = ${myId})`
							);

							// room = createTheRoom(TEST_CANDIDATE_NUM);
							joinTheRoom(room, myId);
						},
						// WebRTC     UI 
						// User    
						// msg   event  
						onmessage: function (msg, jsep) {
							Janus.debug(
								' ::: Got a message (publisher) :::',
								msg
							);

							let event = msg['videoroom'];
							Janus.debug('Event: ' + event);

							if (event) {
								if (event === 'joined') {
									myId = msg['id'];

									Janus.log(
										`${msg['room']}  . ID = ${myId}`
									);

									publishOwnFeed(msg);
								}
							}
							if (jsep) {
								Janus.debug('Handling SDP as well...', jsep);
								videoHandlerOnPC.handleRemoteJsep({
									jsep: jsep,
								});
							}
						},
						// localStreamData 
						onlocalstream: function (stream) {
							Janus.debug(' ::: Got a local stream :::', stream);
							Janus.log('onlocalstream ');

							// html tag  
							let myVideo = document.getElementById('myvideo');
							// tag stream data 
							Janus.attachMediaStream(myVideo, stream);
						},
						oncleanup: function () {},
					});
				},
				error: function (error) {
					Janus.error(error);
				},
				destroyed: function () {
					window.location.reload();
				},
			});
		},
	});
};

/**
 * @description  
 * @param {  } roomID
 */
function joinTheRoom(roomID, userId) {
	let register = {
		request: 'join',
		room: roomID,
		ptype: 'publisher',
		display: "" + userId,
	};

	videoHandlerOnPC.send({
		message: register,
		success: function () {
			Janus.log('  !');
			return true;
		},
		error: function (error) {
			Janus.error('WebRTC error:', error);
			Janus.log('   !');
			return false;
		},
	});
}

/**
 * @description  
 * @param {   } numOfCandidate
 */
function createTheRoom(numOfCandidate) {
	let create = {
		request: 'create',
		bitrate: 500000,
		//   publisher  = (  * 3) + (eyetracker, promoter)
		publishers: numOfCandidate * 3 + 2,
	};

	videoHandlerOnPC.send({
		message: create,
		success: function (result) {
			Janus.log(`room = ${result['room']}  .`);
			//   return
			joinTheRoom(result['room']);
		},
		error: function (error) {
			Janus.error('WebRTC error:', error);
			Janus.log('   !');
			return false;
		},
	});

	return null;
}

/**
 * @description    
 * @param {  } roomNum
 */
function isRoomExist(roomNum) {
	let exists = {
		request: 'exists',
		room: roomNum,
	};

	videoHandlerOnPC.send({
		message: exists,
		success: function () {
			Janus.log(`roomID = ${roomNum}  .`);
			return true;
		},
		error: function (error) {
			Janus.error('WebRTC error:', error);
			Janus.log(`roomID = ${roomNum}   .`);
			return false;
		},
	});
}

/**
 * @description createOffer   webCamFeed 
 */
function publishOwnFeed(msg) {
	videoHandlerOnPC.createOffer({
		media: {
			audioRecv: false,
			videoRecv: false,
			audioSend: false,
			videoSend: true,
		},
		success: function (jsep) {
			Janus.log('Got publisher SDP!', jsep);
			let publish = { request: 'configure', audio: false, video: true };
			videoHandlerOnPC.send({ message: publish, jsep: jsep });

			// localScreenFeed 
			// Janus.log(msg['publishers']['id']);
			// localScreenFeed(msg);
		},
		error: function (error) {
			Janus.error('WebRTC error:', error);
			publishOwnFeed(msg);
		},
	});
}
